import React, { Component } from "react";
import { connect } from "react-redux";
import language from "language";

import get from "get-value";
import set from "set-value";

const LocalizationEnhancer = (ids) => {
  return (ComposedComponent) => {
    class WrappedComponent extends Component {
      constructor(args) {
        super(args);
      }
      render() {
        const l = this.props.language;

        const retPhrases = {};
        ids.forEach(path => {
          set(retPhrases,path,get(language[l], path));
        });

        return <ComposedComponent {...this.props} localizations={retPhrases} />;
      }
    }
    const composedComponentName =
    ComposedComponent.displayName || ComposedComponent.name || "Component";

    Object.keys(ComposedComponent).forEach(propertyName => {
      WrappedComponent[propertyName] = ComposedComponent[propertyName];
    });

    WrappedComponent.displayName = `LocalizationEnhancer(${composedComponentName})`;
    return connect(mapStateToProps,null)(WrappedComponent);
  };
};

function mapStateToProps(state) {
  return {
    language: state.global.get("language")
  };
}

export default LocalizationEnhancer;
