import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import { GlobalSelectors } from "reduxComponents/global";

import { SectionMenuLines } from "./elements";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";

class SectionMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      prevBtnState: "static",
      nextBtnState: "static"
    };
    this.getPrevSection = this.getPrevSection.bind(this);
    this.getNextSection = this.getNextSection.bind(this);
    this.onNextClick = this.onNextClick.bind(this);
    this.onPrevClick = this.onPrevClick.bind(this);
    this.onNextMouseOver = this.onNextMouseOver.bind(this);
    this.onNextMouseLeave = this.onNextMouseLeave.bind(this);
    this.onPrevMouseOver = this.onPrevMouseOver.bind(this);
    this.onPrevMouseLeave = this.onPrevMouseLeave.bind(this);
  }
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
    const sectionChanging =
      nextProps.states.global.getIn(["section", "changingTo"]) !==
      nextProps.states.global.getIn(["section", "changingFrom"]);
    const routeTransitionState = nextProps.states.global.get("routeTransitionState");
    if (sectionChanging) {
      this.setState({
        prevBtnState: "static",
        nextBtnState: "static"
      });
    }
    if (sectionChanging) {
      if (routeTransitionState === "exited" || routeTransitionState === "entering") {
        this.setState({
          prevBtnState: "active",
          nextBtnState: "active"
        });
      }
      if (routeTransitionState === "entered") {
        setTimeout(() => {
          this.setState({
            prevBtnState: "static",
            nextBtnState: "static"
          });
        }, 300);
      }
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onNextClick() {
    const curSection = this.props.activeSection;
    let offset = curSection === "contact" ? 1 : 4;
    const destPath = GlobalSelectors.getDestinationPathnameByOffset(
      this.props.states.global,
      offset
    );

    this.setState({
      nextBtnState: "active"
    });
    if (destPath) {
      this.props.actions.global.requestTransitionPush(destPath, 300, 100, 300);
    }
  }
  onPrevClick() {
    const curSection = this.props.activeSection;
    let offset = curSection === "resume" ? -1 : -4;
    const destPath = GlobalSelectors.getDestinationPathnameByOffset(
      this.props.states.global,
      offset
    );
    this.setState({
      prevBtnState: "active"
    });

    if (destPath) {
      this.props.actions.global.requestTransitionPush(destPath, 300, 100, 300);
    }
  }
  onNextMouseOver() {
    if (this.state.nextBtnState === "static") {
      this.setState({
        nextBtnState: "hover"
      });
    }
  }
  onNextMouseLeave() {
    if (this.state.nextBtnState === "hover") {
      this.setState({
        nextBtnState: "static"
      });
    }
  }
  onPrevMouseOver() {
    if (this.state.prevBtnState === "static") {
      this.setState({
        prevBtnState: "hover"
      });
    }
  }
  onPrevMouseLeave() {
    if (this.state.prevBtnState === "hover") {
      this.setState({
        prevBtnState: "static"
      });
    }
  }
  getPrevSection(curSection) {
    const sections = this.props.states.global.get("sections").toJS();
    const prevIndex = sections.indexOf(curSection) - 1;
    return prevIndex >= 0 ? sections[prevIndex] : null;
  }
  getNextSection(curSection) {
    const sections = this.props.states.global.get("sections").toJS();
    const nextIndex = sections.indexOf(curSection) + 1;
    return nextIndex < sections.length ? sections[nextIndex] : null;
  }
  onMenuClick(item) {
    return event => {
      if (this.props.activeSection !== item) {
        this.props.actions.global.requestTransitionPush(item, 0, 500);
      }
    };
  }
  render() {
    // const startRoute = this.props.states.global.getIn(["route","startRoute"]);
    // const endRoute = this.props.states.global.getIn(["route","endRoute"]);
    // const routeState = this.props.states.global.getIn(["route","state"]);
    const curPage = this.props.states.global.getIn(["page", "cur"]);
    const hasPage = curPage !== null;
    const aboutCls = classNames({
      fill: this.props.activeSection === "about"
    });
    const creationsCls = classNames({
      fill: this.props.activeSection === "creations"
    });
    const contactCls = classNames({
      fill: this.props.activeSection === "contact"
    });
    const resumeCls = classNames({
      fill: this.props.activeSection === "resume"
    });
    const sectionCls = classNames({
      hide: hasPage
    });

    if (this.props.states.global.get("runningInElectron")) {
      return null;
    }

    const prevSection = this.props.localizations.Sections[
      this.getPrevSection(this.props.activeSection)
    ];
    const nextSection = this.props.localizations.Sections[
      this.getNextSection(this.props.activeSection)
    ];

    return (
      <span className="animation fadeIn long ease SectionMenu">
        <div className={`transition ease-in short SectionMenu__container ${sectionCls}`}>
          <SectionMenuLines
            prevOn={prevSection ? true : false}
            nextOn={nextSection ? true : false}
            prevBtnState={this.state.prevBtnState}
            nextBtnState={this.state.nextBtnState}
          />
          <div className="prev-section-container show-mobile">
            <span
              className="btn"
              onClick={this.onPrevClick}
              onMouseOver={this.onPrevMouseOver}
              onMouseLeave={this.onPrevMouseLeave}
            >
              {" "}
              {prevSection}
            </span>
          </div>
          <div className="next-section-container show-mobile">
            <span
              className="btn"
              onMouseOver={this.onNextMouseOver}
              onMouseLeave={this.onNextMouseLeave}
              onClick={this.onNextClick}
            >
              {" "}
              {nextSection}
            </span>
          </div>
          <div className="show-desktop vertical-menu">
            <div className="vertical-menu-row">
              <div className="vertical-menu-item">
                <div onClick={this.onMenuClick("/about")} className={`btn  ${aboutCls}`}>
                  {this.props.localizations.Sections.about}
                </div>
              </div>
              <div className="vertical-menu-item">
                <div onClick={this.onMenuClick("/creations")} className={`btn ${creationsCls}`}>
                  {this.props.localizations.Sections.creations}
                </div>
              </div>
              <div className="vertical-menu-item">
                <div onClick={this.onMenuClick("/contact")} className={`btn ${contactCls}`}>
                  {this.props.localizations.Sections.contact}
                </div>
              </div>
              <div className="vertical-menu-item">
                <div onClick={this.onMenuClick("/resume")} className={`btn ${resumeCls}`}>
                  {this.props.localizations.Sections.resume}
                </div>
              </div>
            </div>
          </div>
        </div>
      </span>
    );
  }
}

SectionMenu.propTypes = {
  //onComplete : PropTypes.func
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  activeSection: PropTypes.oneOf(["about", "creations", "contact", "resume"])
};

SectionMenu.defaultProps = {};
SectionMenu.componentName = "SectionMenu";

export default LocalizationEnhancer(["Sections"])(SectionMenu);
