import React, { PureComponent } from "react";
import ReactDOM from "react-dom";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";
import PageScrollEnhancer from "uiComponents/PageScrollEnhancer/PageScrollEnhancer";

import { ParallaxElement } from "./components";

export class Parallax extends PureComponent{
  constructor(props){
    super(props);
    this.state = {
    };
    this.$ = {
      container: null,
      parallaxContainer: props.scrollRef
    };
    this.update = this.update.bind(this);
    this.scrollHandler = this.scrollHandler.bind(this);
  }
  scrollHandler(event){
    this.props.scroll.handler(event,this.$.parallaxContainer);
  }
  update(ref,props=this.props){
    if(ref && ref !== this.$.parallaxContainer){
      if(this.$.parallaxContainer){
        this.$.parallaxContainer.removeEventListener("scroll",this.scrollHandler,false);
      }
      this.$.parallaxContainer = ref;
      this.$.parallaxContainer.addEventListener("scroll",this.scrollHandler,false);
    }
  }
  componentDidMount(){
    //first time component has mounted
    const reactDOM = ReactDOM.findDOMNode ? ReactDOM : React;
    this.$.container = reactDOM.findDOMNode(this);
    this.update(this.props.scrollRef || this.$.container);
  }
  componentWillUnmount(){
    //when component is unmounted
    if(this.$.parallaxContainer){
      this.$.parallaxContainer.removeEventListener("scroll",this.scrollHandler,false);
    }
  }
  componentWillReceiveProps(nextProps){
    ////invoked when component is receiving new props
    this.update(nextProps.scrollRef,nextProps);
  }
  componentWillUpdate(nextProps, nextState){
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState){
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  updateRef(){
  }
  render(){
    const style = {
      ...this.props.style
    };
    const propsChildren = Array.isArray(this.props.children) ? this.props.children : [this.props.children];
    const children = propsChildren.map((child,i)=>{
      const props = {
        key: i,
        scrolly: this.props.scroll.scrollY
      };
      return React.cloneElement(child,props);
    });
    return(
      <div style={style} className={`Parallax ${this.props.className}`} >
        {children}
      </div>
    );
  }
}

Parallax.propTypes = {
  className: PropTypes.string,
  style: PropTypes.object,
  scrollRef: PropTypes.object
};

Parallax.defaultProps = {
  className: "",
  style: {},
  scrollRef: null
};
Parallax.componentName = "Parallax";

Parallax.Element = ParallaxElement;

export default PageScrollEnhancer(Parallax);
