import React, { Component } from "react";
import PropTypes from "prop-types";
import { Router, Route, Link, browserHistory, IndexRoute } from "react-router";
import classNames from "classnames";

import { GlobalSelectors } from "reduxComponents/global";

import PageNavigation from "uiComponents/PageNavigation/PageNavigation.js";
import SectionTitle from "uiComponents/SectionTitle/SectionTitle.js";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldSectionUpdate } from "clientUtils";

import FacebookIcon from "Assets/svg/facebook.svg.js";
import GithubIcon from "Assets/svg/github.svg.js";
import InstagramIcon from "Assets/svg/instagram.svg.js";
import LinkedinIcon from "Assets/svg/linkedin.svg.js";
import NpmIcon from "Assets/svg/npm.svg.js";
import MailIcon from "Assets/svg/mail.svg.js";
import DeviantArtIcon from "Assets/svg/deviantart.svg.js";
import CodepenIcon from "Assets/svg/codepen.svg.js";

class Contact extends Component {
  constructor(props) {
    super(props);
    this.state = {
      activeIcon: null,
      hoverIcon: null,
      originalSectionTitle: props.localizations.Contact.title,
      originalSectionDescriptions: props.localizations.Contact.descriptions,
      sectionTitle: props.localizations.Contact.title,
      sectionDescriptions: props.localizations.Contact.descriptions
    };
    this.mailTimer = null;
    this.onIconClick = this.onIconClick.bind(this);
    this.onIconHover = this.onIconHover.bind(this);
  }
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    if (this.mailTimer) {
      clearTimeout(this.mailTimer);
    }
    //when component is unmounted
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldSectionUpdate(this.props,nextProps,"contact");
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
    this.setState({
      originalSectionTitle: nextProps.localizations.Contact.title,
      originalSectionDescriptions: nextProps.localizations.Contact.descriptions,
      sectionTitle: nextProps.localizations.Contact.title,
      sectionDescriptions: nextProps.localizations.Contact.descriptions,
      activeIcon: null
    });
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onIconClick(iconType) {
    this.setState({
      activeIcon: iconType
    });
    if (iconType === "mail") {
      //clicked mail
      if (this.state.activeIcon !== "mail") {
        //if current active icon is not mail
        this.setState({
          sectionTitle: "xunchen369@gmail.com",
          sectionDescriptions: []
        });
        if (this.mailTimer) {
          clearTimeout(this.mailTimer);
        }
        this.mailTimer = setTimeout(() => {
          this.setState({
            sectionTitle: this.state.originalSectionTitle,
            sectionDescriptions: this.state.originalSectionDescriptions,
            activeIcon: null
          });
        }, 10000);
      } else {
        //if current active icon is mail
        //set back to default section settings
        this.setState({
          sectionTitle: this.state.originalSectionTitle,
          sectionDescriptions: this.state.originalSectionDescriptions,
          activeIcon: null
        });
      }
    } else {
      // did not click mail
      //set back to default section settings
      this.setState({
        sectionTitle: this.state.originalSectionTitle,
        sectionDescriptions: this.state.originalSectionDescriptions
      });
    }
  }
  onIconHover(iconType) {
    this.setState({
      hoverIcon: iconType
    });
  }
  render() {
    const curPageIndex = GlobalSelectors.findCurPageIndex(this.props.states.global);
    const activeIndex = curPageIndex + 1;
    const fbActiveCls = classNames({
      active: this.state.activeIcon === "facebook"
    });
    const ghActiveCls = classNames({
      active: this.state.activeIcon === "github"
    });
    const igActiveCls = classNames({
      active: this.state.activeIcon === "instagram"
    });
    const npmActiveCls = classNames({
      active: this.state.activeIcon === "npm"
    });
    const liActiveCls = classNames({
      active: this.state.activeIcon === "linkedin"
    });
    const mailActiveCls = classNames({
      active: this.state.activeIcon === "mail"
    });
    const daActiveCls = classNames({
      active: this.state.activeIcon === "deviantart"
    });
    const cpActiveCls = classNames({
      active: this.state.activeIcon === "codepen"
    });
    return (
      <div id="Contact">
        {/* <PageNavigation
            sectionKey="contact"
            pageKey={null}
            actions={this.props.actions}
            states={this.props.states}
          /> */}
        <div className="content">
          <SectionTitle
            title={this.state.sectionTitle}
            descriptions={this.state.sectionDescriptions}
          />
          <div className="contact-links">
            <div className="contact-links-container">
              <a href="https://www.facebook.com/xunchen369" target="_blank">
                <FacebookIcon
                  onMouseOver={() => this.onIconHover("facebook")}
                  onClick={() => this.onIconClick("facebook")}
                  className={`link-icon facebook ${fbActiveCls}`}
                />
              </a>
              <a href="https://www.linkedin.com/in/xun-shawn-chen-68478942/" target="_blank">
                <LinkedinIcon
                  onMouseOver={() => this.onIconHover("linkedin")}
                  onClick={() => this.onIconClick("linkedin")}
                  className={`link-icon linkedin ${liActiveCls}`}
                />
              </a>
              <a href="https://www.instagram.com/xunchen369/" target="_blank">
                <InstagramIcon
                  onMouseOver={() => this.onIconHover("instagram")}
                  onClick={() => this.onIconClick("instagram")}
                  className={`link-icon instagram ${igActiveCls}`}
                />
              </a>
              <MailIcon
                onMouseOver={() => this.onIconHover("mail")}
                onClick={() => this.onIconClick("mail")}
                className={`link-icon mail ${mailActiveCls}`}
              />
            </div>
            <div className="contact-links-container">
              <a href="https://www.npmjs.com/~xunchen369" target="_blank">
                <NpmIcon
                  onMouseOver={() => this.onIconHover("npm")}
                  onClick={() => this.onIconClick("npm")}
                  className={`link-icon npm ${npmActiveCls}`}
                />
              </a>
              <a href="https://www.github.com/gone369" target="_blank">
                <GithubIcon
                  onMouseOver={() => this.onIconHover("github")}
                  onClick={() => this.onIconClick("github")}
                  className={`link-icon github ${ghActiveCls}`}
                />
              </a>
              <a href="https://www.deviantart.com/gone369" target="_blank">
                <DeviantArtIcon
                  onMouseOver={() => this.onIconHover("deviantart")}
                  onClick={() => this.onIconClick("deviantart")}
                  className={`link-icon deviantart ${daActiveCls}`}
                />
              </a>
              <a href="https://codepen.io/gone369" target="_blank">
                <CodepenIcon
                  onMouseOver={() => this.onIconHover("codepen")}
                  onClick={() => this.onIconClick("codepen")}
                  className={`link-icon codepen ${cpActiveCls}`}
                />
              </a>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Contact.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

Contact.defaultProps = {
  //onComplete : ()=>{setTimeout(()=>{},10000)}
};
Contact.componentName = "Contact";

export default LocalizationEnhancer(["Contact"])(Contact);
