import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import Parallax from "uiComponents/Parallax/Parallax.js";
import Page from "uiComponents/Page/Page.js";
import LazyLoad from "react-lazy-load";

import DeskPic from "Assets/img/desk_screens.jpg";
import DashboardPic from "Assets/img/dashboard.jpg";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldPageUpdate } from "clientUtils";

export class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldPageUpdate(this.props,nextProps,"dashboard");
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  render() {
    return (
      <Page
        states={this.props.states}
        actions={this.props.actions}
        title={this.props.localizations.Dashboard.title}
        sectionKey="creations"
        pageKey="dashboard"
        render={state => (
          <div id="Dashboard">
            <Parallax scrollRef={state.root}>
              <div id="dashboard-p1" className="p-container center">
                <p>
                  {this.props.localizations.Dashboard.p1}
                </p>
              </div>
              <Parallax.Element align="center" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"38vw"}
                  width={"100.00%"}
                >
                  <div id="dashboard-pic" className="img-container border">
                    <img src={DashboardPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <div id="dashboard-p2" className="p-container center">
                <p>
                  {this.props.localizations.Dashboard.p2}
                </p>
              </div>
              <Parallax.Element align="center" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"38vw"}
                  width={"100.00%"}
                >
                  <div id="desk-pic" className="img-container border">
                    <img src={DeskPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <div id="dashboard-p3" className="p-container center">
                <p>
                  {this.props.localizations.Dashboard.p3}
                </p>
              </div>
            </Parallax>
          </div>
        )}
      />
    );
  }
}

Dashboard.propTypes = {
  //onComplete : PropTypes.func
};

Dashboard.defaultProps = {
  //onComplete : ()=>{}
};
Dashboard.componentName = "Dashboard";

export default LocalizationEnhancer(["Dashboard"])(Dashboard);
