import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";

export class LocalizationMenu extends Component{
  constructor(props){
    super(props);
    this.state = {
    };
    this.changeLanguage = this.changeLanguage.bind(this);
  }
  componentDidMount(){
    //first time component has mounted
  }
  componentWillUnmount(){
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps){
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps,nextState){
    return true;
  }
  componentWillUpdate(nextProps, nextState){
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState){
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  changeLanguage(lang){
    this.props.actions.global.requestChangeLanguage(lang);
  }
  render(){
    return(
      <div className="LocalizationMenu animation fadeIn long ease" >
        <div className="LocalizationMenu__content container">
          <div className="LocalizationMenu__content__menu">
            <span onClick={()=>this.changeLanguage("en")} className="btn transition short ease-in-out textMask left in">EN</span>
            <span> / </span>
            <span onClick={()=>this.changeLanguage("cn")} className="btn transition short ease-in-out textMask left in">中文</span>
          </div>
        </div>
      </div>
    );
  }
}

LocalizationMenu.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

LocalizationMenu.defaultProps = {
  //onComplete : ()=>{}
};
LocalizationMenu.componentName = "LocalizationMenu";

export default LocalizationMenu;
