export function isPageAtTop(element) {
  const { x, y } = getPageOffsets(element);
  return y === 0;
}
export function isPageAtBottom(element) {
  const { x, y } = getPageOffsets(element);
  const documentHeight = document.body.clientHeight;
  const windowHeight = window.innerHeight;

  return y + windowHeight >= documentHeight;
}
export function getPageOffsets(element) {
  const y = element.scrollTop || window.pageYOffset || document.documentElement.scrollTop;
  const x = element.scrollLeft || window.pageXOffset || document.documentElement.scrollLeft;
  return {
    x,
    y
  };
}
