import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";
import Line from "uiComponents/Line/Line.js";

import FontFaceObserver from "fontfaceobserver";

export class LoadingScreen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false,
      fontLoaded: false,
      readyState: "loading",
      loadingPercentage: 0
    };
    this.frameId = null;
    this.startTime = null;
    this.onStateChange = this.onStateChange.bind(this);
    this.calcLoadingPercentage = this.calcLoadingPercentage.bind(this);
  }
  onStateChange() {
    this.setState({
      readyState: document.readyState
    });
  }
  calcLoadingPercentage(time) {
    if (this.startTime === null) {
      this.startTime = time;
    }
    let loadingPercentage = Math.floor((time - this.startTime) / 20);
    if (this.state.readyState === "loading") {
      loadingPercentage = Math.min(loadingPercentage, 33);
    }
    if (this.state.readyState === "interactive") {
      loadingPercentage = Math.min(loadingPercentage, 67);
    }
    if (this.state.readyState === "complete") {
      loadingPercentage = Math.min(loadingPercentage, 100);
    }
    this.setState({
      loadingPercentage
    });
    if (loadingPercentage === 100 && this.state.fontLoaded) {
      setTimeout(() => {
        this.setState({
          show: true
        });
      }, 1000);
    } else {
      if (this.state.loadingPercentage <= loadingPercentage) {
        requestAnimationFrame(this.calcLoadingPercentage);
      }
    }
  }
  componentWillMount() {
    const promises = [300, 400, 500, 600, 700, 800].reduce((promises, weight, i, weights) => {
      const regFont = new FontFaceObserver("Avenir Next", {
        weight,
        style: "normal"
      });
      const italicFont = new FontFaceObserver("Avenir Next", {
        weight,
        style: "italic"
      });
      return promises.concat([regFont.load(), italicFont.load()]);
    }, []);

    Promise.all(promises).then(() => {
      this.setState({
        fontLoaded: true
      });
    }).catch(e=>{
      console.error(e);
    });
  }
  componentDidMount() {
    //first time component has mounted

    this.frameId = requestAnimationFrame(this.calcLoadingPercentage);
    document.addEventListener("readystatechange", this.onStateChange);
  }
  componentWillUnmount() {
    //when component is unmounted
    if (this.frameId) {
      cancelAnimationFrame(this.frameId);
    }
    document.removeEventListener("readystatechange", this.onStateChange);
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  renderLoading() {
    const hideCls = classNames({
      hide: this.state.loadingPercentage === 100
    });
    return (
      <span className={`transition long ease-in-out loading-container ${hideCls}`}>
        <Line
          className="loading-line"
          direction={"right"}
          placement={"bottom"}
          state={"interpolate"}
          points={{
            interpolate: {
              start: "0",
              end: `${100 - this.state.loadingPercentage}%`
            }
          }}
        />
        <div className="loading-title-container">
          <span className="loading-title">{this.state.loadingPercentage}%</span>
        </div>
      </span>
    );
  }
  render() {
    return (
      <div className="LoadingScreen">
        {this.state.show ? this.props.children : this.renderLoading()}
      </div>
    );
  }
}

LoadingScreen.propTypes = {
  //onComplete : PropTypes.func
};

LoadingScreen.defaultProps = {
  //onComplete : ()=>{}
};
LoadingScreen.componentName = "LoadingScreen";

export default LoadingScreen;
