import React from "react";
import PropTypes from "prop-types";

import Line from "uiComponents/Line/Line.js";
export const SectionMenuLines = props => {
  //<div>{props.children}</div>;
  return (
    <div className="SectionMenuLines show-mobile">
      {props.prevOn && (
        <Line
          className="SectionMenuLines__prev"
          direction={"up"}
          placement={"left"}
          state={props.prevBtnState}
        />
      )}
      {props.nextOn && (
        <Line
          className="SectionMenuLines__next"
          direction={"down"}
          placement={"left"}
          state={props.nextBtnState}
        />
      )}
    </div>
  );
};
SectionMenuLines.propTypes = {
  prevBtnState: PropTypes.oneOf(["static", "hover", "active", "exit"]),
  nextBtnState: PropTypes.oneOf(["static", "hover", "active", "exit"]),
  prevOn: PropTypes.bool,
  nextOn: PropTypes.bool
};
SectionMenuLines.defaultProps = {
  prevOn: false,
  nextOn: false,
  prevBtnState: "static",
  nextBtnState: "static"
};
SectionMenuLines.componentName = "SectionMenuLines";

export default SectionMenuLines;
