import * as ActionTypes from "./global.constants.js";

export default {
  requestTransitionPush: function(destPath,exitDuration,exitingDuration,enterDuration){
    return { type: ActionTypes.REQUEST_TRANSITION_PUSH, destPath,exitDuration,exitingDuration,enterDuration };
  },
  requestChangeLanguage: function(language){
    return { type: ActionTypes.REQUEST_CHANGE_LANGUAGE, language };
  }
};
