import React from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import { capitalize } from "clientUtils";


export const NavigationMenuContent = props => {
  //<div>{props.children}</div>;
  return (
    <div className="NavigationMenuContent">
      <div className="nav-menu">
        {Object.keys(props.pages).map((section, i) => {
          const sectionPages = props.pages[section];
          const sectionName = props.localizationNames[section] || section;
          const menuItems = sectionPages.map((page, j) => {
            const pageName = props.localizationNames[page] || page;
            const activeCls = classNames({
              active: page === props.activePage
            });
            return (
              <div key={j + 1} className="nav-menu-item nav-menu-sub-item">
                <span
                  className="nav-menu-word"
                  onClick={() => props.onItemClick(section, page)}
                  onMouseOver={() => props.onHover(i * 4 + (j + 1))}
                  onMouseLeave={() => props.onHover(null)}
                >
                  <span className={`firstLetter ${activeCls}`}>{capitalize(pageName).charAt(0)}</span>
                  <span className="restWord transition short ease-in-out textMask very-light left in">
                    {pageName.slice(1)}
                  </span>
                </span>
              </div>
            );
          });
          const activeCls = classNames({
            active: section === props.activeSection && props.activePage === null
          });
          return (
            <div key={i} className="nav-menu-row">
              <div key={0} className="nav-menu-item nav-menu-main-item">
                <span
                  className="nav-menu-word"
                  onClick={() => props.onItemClick(section, null)}
                  onMouseOver={() => props.onHover(i * 4)}
                  onMouseLeave={() => props.onHover(null)}
                >
                  <span className={`firstLetter ${activeCls}`}>
                    {capitalize(sectionName).charAt(0)}
                  </span>
                  <span className="restWord transition short ease-in-out textMask very-light left in">
                    {sectionName.slice(1)}
                  </span>
                </span>
              </div>
              {menuItems}
            </div>
          );
        })}
      </div>
    </div>
  );
};

NavigationMenuContent.propTypes = {
  pages: PropTypes.object,
  activePage: PropTypes.string,
  activeSection: PropTypes.string,
  onItemClick: PropTypes.func,
  onHover: PropTypes.func,
  localizationNames: PropTypes.object
};
NavigationMenuContent.defaultProps = {
  activePage: null,
  activeSection: null,
  pages: {},
  onItemClick: () => {},
  onHover: () => {},
  localizationNames: {}
};
NavigationMenuContent.componentName = "NavigationMenuContent";

export default NavigationMenuContent;
