import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";

export class NavigationMenuBackground extends Component {
  constructor(props) {
    super(props);
    this.state = {
      width: null,
      height: null,
      xOffsets: [-36, -32, -32, -19, -45, -58, -34, -33, -39, 0, 0, 0, -43, 0, 0, 0],
      yOffsets: [8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8, 8]
    };
    this.ref = null;
    this.resize = this.resize.bind(this);
    this.getCenterPointAtCell = this.getCenterPointAtCell.bind(this);
    this.calcPoints = this.calcPoints.bind(this);
    this.update = this.update.bind(this);
    this.calculateOffsets = this.calculateOffsets.bind(this);
    this.resizeDebounce = null;
  }
  resize() {
    if (this.resizeDebounce) {
      clearTimeout(this.resizeDebounce);
    }
    this.resizeDebounce = setTimeout(() => {
      if (this.ref) {
        this.setState({
          width: this.ref.clientWidth,
          height: this.ref.clientHeight
        });
        this.forceUpdate();
      }
    }, 400);
  }
  calculateOffsets(menuWords) {
    const xOffsets = Array.from({ length: 16 }, () => 0);
    const yOffsets = Array.from({ length: 16 }, () => 0);

    menuWords.forEach((menuWord, i) => {
      if (menuWord === null) {
        return;
      }
      const firstChar = menuWord.childNodes[0];
      const firstCharRect = firstChar.getBoundingClientRect();
      const fw = firstCharRect.width;
      const fh = firstCharRect.height;

      const rect = menuWord.getBoundingClientRect();
      const w = rect.width;
      const h = rect.height;
      xOffsets[i] = -1*(w/2-fw/2);
      yOffsets[i] = h/3;
    });

    this.setState({
      xOffsets,
      yOffsets
    });
  }
  update() {
    let menuWords = Array.from(document.getElementsByClassName("nav-menu-word"));
    menuWords = menuWords.concat([null, null, menuWords[9], null, null, null]);
    menuWords[9] = null;
    //convert to 16 cell array;
    this.calculateOffsets(menuWords);
  }
  componentDidMount() {
    //first time component has mounted
    this.resize();
    this.update(this.props);
    window.addEventListener("resize", this.resize, false);
  }
  componentWillUnmount() {
    //when component is unmounted
    window.removeEventListener("resize", this.resize, false);
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
    this.update(nextProps);
  }
  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  getCenterPointAtCell(index) {
    const h = this.state.height;
    const w = this.state.width;
    const i = index % 4;
    const x = w / 4 * i + w / 8 + this.state.xOffsets[index];
    const y = h / 4 * Math.floor(index / 4) + h / 8 + this.state.yOffsets[index];

    return {
      x,
      y
    };
  }
  calcPoints(routeTransitionState) {
    const pages = this.props.states.global.get("pages").toJS();
    const sections = Object.keys(pages);
    const grid = sections.reduce((sumGrid, section, i) => {
      const sectionPages = pages[section];
      let retPages = [section, null, null, null];
      sectionPages.forEach((page, j) => {
        retPages[j + 1] = page;
      });
      return sumGrid.concat(retPages);
    }, []);

    //calc start point

    const activePage = this.props.states.global.getIn(["page", "cur"], null);
    const activeSection = this.props.states.global.getIn(["section", "cur"], null);
    let activeGridPageKey = activePage ? activePage : activeSection;

    const startPointGridIndex = grid.indexOf(activeGridPageKey);

    const startPoint = this.getCenterPointAtCell(startPointGridIndex);
    let x1 = startPoint.x;
    let y1 = startPoint.y;

    let x2, y2;

    //calc end point

    if (routeTransitionState === "entered" || routeTransitionState === null) {
      //find hover point
      const hoverPoint = this.getCenterPointAtCell(this.props.hoverGridIndex);
      x2 = hoverPoint.x || x1;
      y2 = hoverPoint.y || y1;
    } else {
      //find transition point

      const fromPage = this.props.states.global.getIn(["page", "changingFrom"], null);
      const fromSection = this.props.states.global.getIn(["section", "changingFrom"], null);

      const toPage = this.props.states.global.getIn(["page", "changingTo"], null);
      const toSection = this.props.states.global.getIn(["section", "changingTo"], null);

      let fromGridPageKey = fromPage ? fromPage : fromSection;
      let toGridPageKey = toPage ? toPage : toSection;

      const fromPointGridIndex = grid.indexOf(fromGridPageKey);
      const toPointGridIndex = grid.indexOf(toGridPageKey);

      const fromPoint = this.getCenterPointAtCell(fromPointGridIndex);
      const toPoint = this.getCenterPointAtCell(toPointGridIndex);

      x1 = fromPoint.x;
      y1 = fromPoint.y;

      x2 = toPoint.x;
      y2 = toPoint.y;
    }

    // console.log("points",x1,y1,x2,y2);

    return {
      x1,
      x2,
      y1,
      y2
    };
  }
  calcLineLength(x1, x2, y1, y2) {
    return Math.sqrt(Math.pow(x2 - x1, 2) + Math.pow(y2 - y1, 2));
  }
  render() {
    const routeTransitionState = this.props.states.global.get("routeTransitionState");
    const { x1, x2, y1, y2 } = this.calcPoints(routeTransitionState);
    const length = 2 * Math.round(this.calcLineLength(x1, x2, y1, y2));

    let offset = length;
    if (this.props.isHovering || ["exiting", "exited"].includes(routeTransitionState)) {
      offset = 0;
    }
    if (routeTransitionState === "entering") {
      offset = -1 * length;
    }

    const lineCls = classNames({
      static: offset === length,
      hover: offset === 0
    });

    return (
      <div ref={r => (this.ref = r)} className="NavigationMenuBackground">
        <svg>
          <line
            className={`${lineCls} ${routeTransitionState}`}
            strokeDasharray={length}
            strokeDashoffset={offset}
            x1={x1}
            x2={x2}
            y1={y1}
            y2={y2}
          />
        </svg>
      </div>
    );
  }
}

NavigationMenuBackground.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  hoverGridIndex: PropTypes.number,
  isHovering: PropTypes.bool
};

NavigationMenuBackground.defaultProps = {
  hoverGridIndex: null,
  isHover: false
};
NavigationMenuBackground.componentName = "NavigationMenuBackground";

export default NavigationMenuBackground;
