import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import Parallax from "uiComponents/Parallax/Parallax.js";
import Page from "uiComponents/Page/Page.js";
import LazyLoad from "react-lazy-load";

import benchPic from "Assets/img/bench.jpg";
import ttPic from "Assets/img/tt_web.jpg";
import pianoPic from "Assets/img/piano_web.jpg";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldPageUpdate } from "clientUtils";

class Passion extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldPageUpdate(this.props,nextProps,"passion");
  }
  render() {
    return (
      <Page
        states={this.props.states}
        actions={this.props.actions}
        title={this.props.localizations.Passion.title}
        sectionKey="about"
        pageKey="passion"
        render={state => (
          <div id="Passion">
            <Parallax scrollRef={state.root}>
              <Parallax.Element align="right" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"60vw"}
                  width={"100.00%"}
                >
                  <div id="bench-pic" className="img-container">
                    <img src={benchPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="travel-title" className="t-container center">
                  <span>{this.props.localizations.Passion.t1}</span>
                </div>
              </Parallax.Element>
              <div id="passion-p1" className="p-container">
                <p>{this.props.localizations.Passion.p1}</p>
              </div>
              <Parallax.Element align="left" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"50vw"}
                  width={"100%"}
                >
                  <div id="tt-pic" className="img-container">
                    <img src={ttPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="sports-title" className="t-container center">
                  <span>{this.props.localizations.Passion.t2}</span>
                </div>
              </Parallax.Element>
              <div id="passion-p2" className="p-container right">
                <p>{this.props.localizations.Passion.p2}</p>
              </div>
              <Parallax.Element align="center" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"40vw"}
                  width={"100%"}
                >
                  <div id="piano-pic" className="img-container">
                    <img src={pianoPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="music-title" className="t-container center">
                  <span>{this.props.localizations.Passion.t3}</span>
                </div>
              </Parallax.Element>
              <div id="passion-p3" className="p-container center">
                <p>{this.props.localizations.Passion.p3}</p>
              </div>
            </Parallax>
          </div>
        )}
      />
    );
  }
}

Passion.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

Passion.defaultProps = {
  //onComplete : ()=>{setTimeout(()=>{},10000)}
};
Passion.componentName = "Passion";

export default LocalizationEnhancer(["Passion"])(Passion);
