import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import Parallax from "uiComponents/Parallax/Parallax.js";
import LazyLoad from "react-lazy-load";

import DelphiPic from "Assets/img/delphi_web.jpg";
import SanJosePic from "Assets/img/sanjose_web.jpg";
import TargetPic from "Assets/img/target_web.jpg";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldPageUpdate } from "clientUtils";

import Page from "uiComponents/Page/Page.js";

class Work extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldPageUpdate(this.props,nextProps, "career");
  }
  render() {
    return (
      <Page
        states={this.props.states}
        actions={this.props.actions}
        title={this.props.localizations.Career.title}
        sectionKey="about"
        pageKey="career"
        render={state => (
          <div id="Work">
            <Parallax scrollRef={state.root}>
              <Parallax.Element align="right" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"40vw"}
                  width={"100.00%"}
                >
                  <div id="delphi-pic" className="img-container">
                    <img src={DelphiPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="delphi-title" className="t-container center">
                  <span>{this.props.localizations.Career.t1}</span>
                </div>
              </Parallax.Element>
              <div id="work-p1" className="p-container center">
                <p>
                  {this.props.localizations.Career.p1}
                </p>
              </div>
              <Parallax.Element align="left" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"50vw"}
                  width={"100.00%"}
                >
                  <div id="sanjose-pic" className="img-container">
                    <img src={SanJosePic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="sanjose-title" className="t-container">
                  <span>{this.props.localizations.Career.t2}</span>
                </div>
              </Parallax.Element>
              <div id="work-p2" className="p-container right">
                <p>
                  {this.props.localizations.Career.p2}
                </p>
              </div>
              <Parallax.Element align="right" percentage={5} reverse={true}>
                <div id="target-pic" className="img-container">
                  <LazyLoad
                    offsetVerticle={-200}
                    throttle={50}
                    debounce={false}
                    height={"50vw"}
                    width={"100%"}
                  >
                    <img src={TargetPic} />
                  </LazyLoad>
                </div>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="target-title" className="t-container right">
                  <span>{this.props.localizations.Career.t3}</span>
                </div>
              </Parallax.Element>
              <div id="work-p3" className="p-container center">
                <p>
                  {this.props.localizations.Career.p3}
                </p>
              </div>
            </Parallax>
          </div>
        )}
      />
    );
  }
}

Work.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

Work.defaultProps = {
  //onComplete : ()=>{setTimeout(()=>{},10000)}
};
Work.componentName = "Work";

export default LocalizationEnhancer(["Career"])(Work);
