import { delay } from "redux-saga";
import { put, call, take, takeEvery, select, fork } from "redux-saga/effects";
import * as ActionTypes from "./global.constants.js";
import { push } from "react-router-redux";
import { getPage, getSection } from "./global.selectors.js";

export function* onTransitionPush({
  destPath,
  enterDuration = 500,
  exitingDuration = 500,
  exitDuration = 500
}) {
  try {
    const section = getSection(destPath);
    const page = getPage(destPath);
    yield put({ type: ActionTypes.UPDATE_CHANGING_PAGE, page: page });
    yield put({ type: ActionTypes.UPDATE_CHANGING_SECTION, section: section });
    yield put({
      type: ActionTypes.UPDATE_ROUTE_TRANSITION_STATE,
      state: "exiting"
    });
    yield call(delay, exitDuration);
    yield put({
      type: ActionTypes.UPDATE_ROUTE_TRANSITION_STATE,
      state: "exited"
    });

    yield put(push(destPath));

    yield call(delay, exitingDuration);

    yield put({
      type: ActionTypes.UPDATE_ROUTE_TRANSITION_STATE,
      state: "entering" // ["static","enter","leave"]
    });

    yield call(delay, enterDuration);

    yield put({
      type: ActionTypes.UPDATE_ROUTE_TRANSITION_STATE,
      state: "entered" // ["static","enter","leave"]
    });
  } catch (e) {
    console.error(e);
  }
}

export function* onLocationChange({ payload }) {
  try {
    const section = getSection(payload.pathname);
    const page = getPage(payload.pathname);
    yield put({ type: ActionTypes.UPDATE_PAGE, page: page });
    yield put({ type: ActionTypes.UPDATE_SECTION, section: section });
  } catch (e) {
    console.error(e);
  }
}

export function* onChangeLanguage({ language }) {
  try {
    yield put({ type: ActionTypes.CHANGE_LANGUAGE, language });
    window.localStorage.setItem("language",language);
  } catch (e) {
    console.error(e);
  }
}

export default {
  watchGlobal: function* watchGlobal() {
    yield takeEvery(ActionTypes.REQUEST_TRANSITION_PUSH, onTransitionPush);
    yield takeEvery("@@router/LOCATION_CHANGE", onLocationChange);
    yield takeEvery(ActionTypes.REQUEST_CHANGE_LANGUAGE, onChangeLanguage);
  }
};
