import React, { Component } from "react";
import PropTypes from "prop-types";
import { Route, Redirect, Switch } from "react-router-dom";
import classNames from "classnames";

import { GlobalSelectors } from "reduxComponents/global";

import { ResumePage } from "./elements";
import { shouldSectionUpdate } from "clientUtils";

import SectionTitle from "uiComponents/SectionTitle/SectionTitle.js";

class Resume extends Component {
  constructor(props) {
    super(props);
    this.state = {
      mountPage: false,
      downloadHidden: true
    };
    this.update = this.update.bind(this);
    this.onDownloadClick = this.onDownloadClick.bind(this);
  }
  update(props) {
    const section = props.states.global.getIn(["section", "cur"]);
    this.setState({
      mountPage: section === "resume"
    });
    if (section === "resume") {
      setTimeout(() => {
        document.body.dispatchEvent(new CustomEvent("view-ready", {}));
      }, 5000);
    }
  }
  componentDidMount() {
    //first time component has mounted
    this.update(this.props);
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
    this.update(nextProps);
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldSectionUpdate(this.props,nextProps,"resume");
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onDownloadClick(){
    this.setState({
      downloadHidden: !this.state.downloadHidden
    });
  }
  render() {
    const runningInElectron = this.props.states.global.get("runningInElectron");
    return (
      <div className="Page" id="Resume">
        <div className="Page__container">
          <div className="Page__container__content content">
            {this.state.mountPage && (<ResumePage downloadHidden={this.state.downloadHidden} onDownloadClick={this.onDownloadClick} runningInElectron={runningInElectron}/>)}
          </div>
        </div>
      </div>
    );
  }
}

Resume.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

Resume.defaultProps = {};
Resume.componentName = "Resume";

export default Resume;
