import React, { PureComponent } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";

import PageNavigation from "uiComponents/PageNavigation/PageNavigation.js";
import Parallax from "uiComponents/Parallax/Parallax.js";
import PageHeader from "uiComponents/PageHeader/PageHeader.js";

export class Page extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      root: null
    };
    this.onNextClick = this.onNextClick.bind(this);
    this.refRoot = this.refRoot.bind(this);
  }
  refRoot(r) {
    this.setState({
      root: r
    });
  }
  onNextClick() {
    setTimeout(() => {
      this.state.root.scroll({top: 0,behavior:"smooth"});
    }, 1100);
  }
  render() {
    return (
      <div className="Page">
        <div ref={this.refRoot} className="Page__container">
          <div className="relative">
            <PageNavigation
              sectionKey={this.props.sectionKey}
              pageKey={this.props.pageKey}
              scrollRef={this.state.root}
              onNextClick={this.onNextClick}
              actions={this.props.actions}
              states={this.props.states}
            />
            <PageHeader root={this.state.root} title={this.props.title}/>
            <div className="Page__container__content">
              {this.props.render(this.state)}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

Page.propTypes = {
  sectionKey: PropTypes.string.isRequired,
  pageKey: PropTypes.string.isRequired,
  title: PropTypes.string,
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })

};

Page.defaultProps = {
  title: "",
  sectionKey: null,
  pageKey: null
};
Page.componentName = "Page";

export default Page;
