import React, { Component } from "react";
import PropTypes from "prop-types";

import { Spring } from "react-spring";

export class ParallaxElement extends Component {
  constructor(props) {
    super(props);
    this.ref = null;
    this.state = {
      yParallaxOffset: 0
    };
    this.calcNewOffset = this.calcNewOffset.bind(this);
  }
  calcNewOffset() {
    const scrolly = this.props.scrolly;
    //get ParallaxElement position
    let yParallaxOffset = 0;
    if (this.ref) {
      const refYPosition = this.ref.offsetTop;
      const range = scrolly - refYPosition;
      const parallaxPercentage = this.props.percentage / 100;
      yParallaxOffset = (this.props.reverse ? -1 : 1) * range * parallaxPercentage;
    }
    return yParallaxOffset;
  }
  getStyle(y) {
    return {
      transform: `translate(0,${y}px)`
    };
  }
  render() {
    const newYParallaxOffset = this.calcNewOffset();
    return (
      <Spring
        config={{
          tension: 280,
          friction: 60
        }}
        from={{
          y: this.state.yParallaxOffset
        }}
        to={{
          y: newYParallaxOffset
        }}
      >
        {({ y }) => {
          return (
            <div
              ref={r => (this.ref = r)}
              style={this.getStyle(y)}
              className={`ParallaxElement ${this.props.align}`}
            >
              {this.props.children}
            </div>
          );
        }}
      </Spring>
    );
  }
}

ParallaxElement.propTypes = {
  className: PropTypes.string,
  scrolly: PropTypes.number.isRequired,
  percentage: PropTypes.number,
  reverse: PropTypes.bool,
  align: PropTypes.oneOf(["left", "right", "center"])
};
ParallaxElement.defaultProps = {
  className: "",
  scrolly: 0,
  percentage: 5,
  reverse: false,
  align: "left"
};
ParallaxElement.componentName = "ParallaxElement";

export default ParallaxElement;
