import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import Parallax from "uiComponents/Parallax/Parallax.js";
import LazyLoad from "react-lazy-load";

import UIUCPic from "Assets/img/uiuc_south_quad_web.jpg";
import SanDiegoPic from "Assets/img/san_diego_web.jpg";
import ShanghaiPic from "Assets/img/shanghai_web.jpg";

import Page from "uiComponents/Page/Page.js";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldPageUpdate } from "clientUtils";

class Intro extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldPageUpdate(this.props,nextProps, "intro");
  }
  componentDidMount() {}
  componentDidUpdate() {}
  render() {
    return (
      <Page
        states={this.props.states}
        actions={this.props.actions}
        sectionKey="about"
        pageKey="intro"
        title={this.props.localizations.Intro.title}
        render={state => (
          <div id="Intro">
            <Parallax scrollRef={state.root}>
              <Parallax.Element percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"50vw"}
                  width={"100%"}
                >
                  <div id="sandiego-pic" className="img-container">
                    <img src={SanDiegoPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="about-me-title" className="t-container center">
                  <span>{this.props.localizations.Intro.t1}</span>
                </div>
              </Parallax.Element>
              <div id="intro-p1" className="p-container right">
                <p>
                  {this.props.localizations.Intro.p1}
                </p>
              </div>
              <Parallax.Element percentage={5} reverse={true} align="right">
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"50vw"}
                  width={"100%"}
                >
                  <div id="shanghai-pic" className="img-container">
                    <img src={ShanghaiPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="shanghai-title" className="t-container right">
                  <span>{this.props.localizations.Intro.t2}</span>
                </div>
              </Parallax.Element>
              <div id="intro-p2" className="p-container">
                <p>
                  {this.props.localizations.Intro.p2}
                </p>
              </div>
              <Parallax.Element percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"50vw"}
                  width={"100%"}
                >
                  <div id="uiuc-pic" className="img-container">
                    <img src={UIUCPic} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <Parallax.Element percentage={5}>
                <div id="uiuc-title" className="t-container right">
                  <span>{this.props.localizations.Intro.t3}</span>
                </div>
              </Parallax.Element>
              <div id="intro-p3" className="p-container center">
                <p>
                  {this.props.localizations.Intro.p3}
                </p>
              </div>
            </Parallax>
          </div>
        )}
      />
    );
  }
}

Intro.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

Intro.defaultProps = {
  //onComplete : PropTypes.func
};
Intro.componentName = "Intro";

export default LocalizationEnhancer([
  "Intro"
])(Intro);
