import React, { Component } from "react";
import * as pageScrollHelper from "clientUtils/pageScroll.helper.js";

export const PageScrollEnhancer = ComposedComponent => {
  class WrappedComponent extends Component {
    constructor(args) {
      super(args);
      this.state = {
        pageAtTop: false,
        pageAtBottom: false,
        scrollX: 0,
        scrollY: 0
      };
      this.onScroll = this.onScroll.bind(this);
    }
    onScroll(event,element) {
      const pageAtTop = pageScrollHelper.isPageAtTop(element);
      const pageAtBottom = pageScrollHelper.isPageAtBottom(element);
      const { x, y } = pageScrollHelper.getPageOffsets(element);
      this.setState({
        pageAtTop,
        pageAtBottom,
        scrollX: x,
        scrollY: y
      });
    }
    render() {
      const scrollProps = {
        handler: this.onScroll,
        ...this.state
      };
      return (
        <ComposedComponent scroll={scrollProps} {...this.props}>
          {this.props.children}
        </ComposedComponent>
      );
    }
  }
  const composedComponentName =
    ComposedComponent.displayName || ComposedComponent.name || "Component";

  Object.keys(ComposedComponent).forEach(propertyName => {
    WrappedComponent[propertyName] = ComposedComponent[propertyName];
  });

  WrappedComponent.displayName = `PageScrollEnhancer(${composedComponentName})`;

  return WrappedComponent;
};

export default PageScrollEnhancer;
