import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";
import { bindActionCreators } from "redux";
import { connect } from "react-redux";
import { GlobalActions, GlobalSelectors } from "reduxComponents/global/index.js";
import { Route, Redirect, Switch } from "react-router-dom";

import { About, Creations, Contact, Resume } from "./routes";
import {
  LoadingScreen,
  LocalizationMenu,
  SectionMenu,
  NavigationMenu,
  PageLines
} from "./components";

import * as pageScrollHelper from "clientUtils/pageScroll.helper.js";

import Slide from "uiComponents/Slide/Slide.js";

import { detect } from "detect-browser";
const browser = detect();

const NoMatch = () => <Redirect to="/about" />;

class Index extends Component {
  constructor(props) {
    super(props);
    this.state = {
      timer:  0
    };
    this.timer = null;
    this.renderNavigationMenu = this.renderNavigationMenu.bind(this);
    this.renderLocalizationMenu = this.renderLocalizationMenu.bind(this);
  }
  componentDidMount() {
    this.timer = setInterval(()=>{
      this.setState({
        timer: this.state.timer + 1
      });
    },600);
  }
  componentWillUnmount() {
    clearInterval(this.timer);
  }
  componentDidUpdate() {
    if(this.state.timer === 6){
      clearInterval(this.timer);
    }
  }
  componentWillReceiveProps(nextProps) {
    //invoked when component is receiving new props
    // not called for initial rendering
    // Use this as an opportunity to react to a prop transition before render() is called by updating the state using this.setState(). The old props can be accessed via this.props. Calling this.setState() within this function will not trigger an additional render.
  }
  renderNavigationMenu() {
    if(this.state.timer >= 3){
      return (
        <NavigationMenu
          // match={this.props.match}
          states={this.props.states}
          actions={this.props.actions}
        />
      );
    }
  }
  renderLocalizationMenu() {
    if(this.state.timer >= 4){
      return <LocalizationMenu states={this.props.states} actions={this.props.actions} />;
    }
  }
  renderSectionMenu(activeSection) {
    if(this.state.timer >= 5){
      return (
        <SectionMenu
          activeSection={activeSection}
          states={this.props.states}
          actions={this.props.actions}
        />
      );
    }
  }
  renderSlides(activeSectionIndex) {
    if(this.state.timer >= 6){
      return (
        <Slide axis={"vertical"} activeIndex={activeSectionIndex}>
          <About match={this.props.match} states={this.props.states} actions={this.props.actions} />
          <Creations
            match={this.props.match}
            states={this.props.states}
            actions={this.props.actions}
          />
          <Contact match={this.props.match} states={this.props.states} actions={this.props.actions} />
          <Resume match={this.props.match} states={this.props.states} actions={this.props.actions} />
        </Slide>
      );
    }
  }
  render() {
    const states = this.props.states;
    const actions = this.props.actions;
    const activeSection = this.props.match.params.section;
    // const curSection = this.props.state.global.
    const activeSectionIndex = GlobalSelectors.findSectionIndex(
      activeSection,
      this.props.states.global.get("sections")
    );
    if (activeSectionIndex === -1) {
      return <NoMatch />;
    }
    const themeCls = classNames({
      "light-theme": ["contact", "resume"].includes(activeSection),
      "gold-theme": activeSection === "creations",
      "dark-theme": activeSection === "about"
    });
    const pages = this.props.states.global.get("pages").toJS();
    return (
      <div key={"index"} id="index" className={`${themeCls} ${browser.name}`}>
        <LoadingScreen>
          {this.renderNavigationMenu()}
          {this.renderLocalizationMenu()}
          <div className="container absolute">
            <div className="content">{this.renderSectionMenu(activeSection)}</div>
          </div>
          {this.renderSlides(activeSectionIndex)}
        </LoadingScreen>
      </div>
    );
  }
}

Index.propTypes = {
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  states: PropTypes.shape({
    global: PropTypes.object.isRequired,
    router: PropTypes.object.isRequired
  })
};

function mapStateToProps(state) {
  return {
    states: {
      global: state.global,
      router: state.router
    }
  };
}

function mapDispatchToProps(dispatch) {
  return {
    actions: {
      global: bindActionCreators(GlobalActions, dispatch)
    }
  };
}

export default connect(mapStateToProps, mapDispatchToProps)(Index);
