import * as ActionTypes from "./global.constants.js";
import { initialState } from "./global.state.js";
import { fromJS } from "immutable";

export default function global(state = initialState, action) {

  const reducers = {
    [ActionTypes.UPDATE_ROUTE_TRANSITION_STATE]: function(state,action){
      return state.set("routeTransitionState",action.state);
    },
    [ActionTypes.UPDATE_CHANGING_PAGE]: function(state,action){
      const oldCurPage = state.getIn(["page","cur"]);
      return state.setIn(["page","changingFrom"],oldCurPage).setIn(["page","changingTo"],action.page);
    },
    [ActionTypes.UPDATE_PAGE]: function(state,action){
      const oldCurPage = state.getIn(["page","cur"]);
      return state.setIn(["page","prev"],oldCurPage).setIn(["page","cur"],action.page);
    },
    [ActionTypes.UPDATE_CHANGING_SECTION]: function(state,action){
      const oldCurSection = state.getIn(["section","cur"]);
      return state.setIn(["section","changingFrom"],oldCurSection).setIn(["section","changingTo"],action.section);
    },
    [ActionTypes.UPDATE_SECTION]: function(state,action){
      const oldCurSection = state.getIn(["section","cur"]);
      return state.setIn(["section","prev"],oldCurSection).setIn(["section","cur"],action.section);
    },
    [ActionTypes.CHANGE_LANGUAGE]: function(state,action){
      return state.set("language",action.language);
    }
  };

  const reducer = reducers[action.type];
  if(typeof reducer !== "undefined"){
    return reducer(state,action);
  } else {
    return state;
  }
}
