import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

class Line extends Component{
  constructor(props){
    super(props);
    this.state = {
    };
  }
  componentDidMount(){
    //first time component has mounted
  }
  componentWillUnmount(){
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps){
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps,nextState){
    return true;
  }
  componentWillUpdate(nextProps, nextState){
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState){
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  getLineStyle(direction=null,placement=null,state=null,points=null){
    if(points === null || direction === null || placement === null || state === null){
      return {};
    }
    const style = {};
    const start = points[state].start;

    //get end
    let end = points[state].end;
    let sign = "+";
    if(typeof end === "number"){
      end = Math.abs(end)+"px";
      sign = points[state].end >= 0 ? "-" : "+";
    } else {
      if(end.charAt(0) === "-"){
        end = end.substr(1);
        sign = "+";
      } else {
        sign = "-";
      }
    }

    if(["left","right"].includes(placement)){
      //vertical
      if(direction === "up"){
        style.bottom = start;
        style.top = state === "interpolate" ? end : `calc( 100% ${sign} ${end} )`;
      } else if(direction === "down"){
        style.top = start;
        style.bottom = state === "interpolate" ? end : `calc( 100% ${sign} ${end} )`;
      }
    } else {
      if(direction === "left"){
        style.right = start;
        style.left = state === "interpolate" ? end : `calc( 100% ${sign} ${end} )`;
      } else if(direction === "right"){
        style.left = start;
        style.right = state === "interpolate" ? end : `calc( 100% ${sign} ${end} )`;
      }
    }

    return style;
  }
  render(){
    const lineStyle = this.getLineStyle(this.props.direction, this.props.placement, this.props.state, this.props.points);
    return(
      <div className="Line">
        <div className="line-container">
          <div style={lineStyle} className={`Line__content line ${this.props.className} ${this.props.placement} ${this.props.state}`}></div>
        </div>
      </div>
    );
  }
}

Line.propTypes = {
  direction: PropTypes.oneOf(["left","up","right","down"]).isRequired,
  placement: PropTypes.oneOf(["top","left","right","bottom"]).isRequired,
  state: PropTypes.oneOf(["static","hover","enter","active","exit","interpolate"]),
  points: PropTypes.shape({
    static: PropTypes.shape({
      start:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
      end:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
    }),
    hover: PropTypes.shape({
      start:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
      end:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
    }),
    enter: PropTypes.shape({
      start:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
      end:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
    }),
    active: PropTypes.shape({
      start:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
      end:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
    }),
    exit: PropTypes.shape({
      start:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
      end:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
    }),
    interpolate: PropTypes.shape({
      start:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired,
      end:PropTypes.oneOfType([PropTypes.string,PropTypes.number]).isRequired
    }),
  }),
};

Line.defaultProps = {
  state: "static",
  points: null
};

Line.componentName = "Line";

export default Line;
