import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";

import Line from "uiComponents/Line/Line.js";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";

export class PageHeader extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lineState: "static"
    };
    this.onScrollOver = this.onScrollOver.bind(this);
    this.onScrollDown = this.onScrollDown.bind(this);
    this.onScrollLeave = this.onScrollLeave.bind(this);
  }
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onScrollLeave() {
    this.setState({
      lineState: "static"
    });
  }
  onScrollOver() {
    this.setState({
      lineState: "hover"
    });
  }
  onScrollDown() {
    this.setState({
      lineState: "active"
    });
    setTimeout(() => {
      this.props.root.scrollBy({ top: window.innerHeight, behavior: "smooth" });
    }, 150);
  }
  render() {
    return (
      <div className="PageHeader">
        <h1
          onMouseOver={this.onScrollOver}
          onMouseLeave={this.onScrollLeave}
          onClick={this.onScrollDown}
          className="PageHeader__title transition short ease-in-out textMask left in pointer"
        >
          {this.props.title}
        </h1>
        <Line
          className="scroll-down-line"
          direction={"down"}
          placement={"right"}
          state={this.state.lineState}
        />
        <div className="PageHeader__container container">
          <div className="scroll-down-container">
            <div className="scroll-down-btn">
              <span
                onMouseLeave={this.onScrollLeave}
                onMouseOver={this.onScrollOver}
                onClick={this.onScrollDown}
                className="transition short ease-in-out textMask left in"
              >
                {this.props.localizations.PageHeader.scroll}
              </span>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

PageHeader.propTypes = {
  root: PropTypes.object
};

PageHeader.defaultProps = {};
PageHeader.componentName = "PageHeader";

export default LocalizationEnhancer(["PageHeader"])(PageHeader);
