import React, { Component } from "react";
import ReactDOM from "react-dom";
import PropTypes from "prop-types";
import classNames from "classnames";

import Line from "uiComponents/Line/Line.js";
import { PageNavigationLines } from "./elements";
import { PageNavigationScrollLines } from "./components";
import { GlobalSelectors } from "reduxComponents/global";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import PageScrollEnhancer from "uiComponents/PageScrollEnhancer/PageScrollEnhancer";

class PageNavigation extends Component {
  constructor(props) {
    super(props);
    this.state = {
      prevBtnState: "static",
      nextBtnState: "static"
    };
    this.$ = {};
    this.onNextClick = this.onNextClick.bind(this);
    this.onPrevClick = this.onPrevClick.bind(this);

    this.onNextMouseOver = this.onNextMouseOver.bind(this);
    this.onNextMouseLeave = this.onNextMouseLeave.bind(this);
    this.onPrevMouseOver = this.onPrevMouseOver.bind(this);
    this.onPrevMouseLeave = this.onPrevMouseLeave.bind(this);
    this.onResize = this.onResize.bind(this);
  }
  onResize() {
    if (this.$.root) {
      const pageHeight = this.$.root.clientHeight;
      this.setState({
        pageHeight
      });
    }
  }
  componentDidMount() {
    setTimeout(()=>{
      this.onResize();
    },1);
    window.addEventListener("resize", this.onResize, false);
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
    window.removeEventListener("resize", this.onResize, false);
  }
  componentWillReceiveProps(nextProps, nextState) {
    ////invoked when component is receiving new props
    const pageChanging =
      nextProps.states.global.getIn(["page", "changingTo"]) !==
      nextProps.states.global.getIn(["page", "changingFrom"]);
    const sectionChanging =
      nextProps.states.global.getIn(["section", "changingTo"]) !==
      nextProps.states.global.getIn(["section", "changingFrom"]);
    const routeTransitionState = nextProps.states.global.get("routeTransitionState");
    if (sectionChanging) {
      this.setState({
        prevBtnState: "static",
        nextBtnState: "static"
      });
    }
    if (pageChanging) {
      if (routeTransitionState === "exited" || routeTransitionState === "entering") {
        this.setState({
          prevBtnState: "active",
          nextBtnState: "active"
        });
      }
      if (routeTransitionState === "entered") {
        setTimeout(() => {
          this.setState({
            prevBtnState: "static",
            nextBtnState: "static"
          });
        }, 300);
      }
    }
  }
  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onNextClick() {
    const destPath = GlobalSelectors.getDestinationPathnameByOffset(this.props.states.global, 1);
    this.setState({
      nextBtnState: "active"
    });
    if (destPath) {
      this.props.actions.global.requestTransitionPush(destPath, 300, 100, 300);
    }
    this.props.onNextClick(destPath);
  }
  onPrevClick() {
    const destPath = GlobalSelectors.getDestinationPathnameByOffset(this.props.states.global, -1);
    this.setState({
      prevBtnState: "active"
    });

    if (destPath) {
      this.props.actions.global.requestTransitionPush(destPath, 300, 100, 300);
    }
  }
  onNextMouseOver() {
    if (this.state.nextBtnState === "static") {
      this.setState({
        nextBtnState: "hover"
      });
    }
  }
  onNextMouseLeave() {
    if (this.state.nextBtnState === "hover") {
      this.setState({
        nextBtnState: "static"
      });
    }
  }
  onPrevMouseOver() {
    if (this.state.prevBtnState === "static") {
      this.setState({
        prevBtnState: "hover"
      });
    }
  }
  onPrevMouseLeave() {
    if (this.state.prevBtnState === "hover") {
      this.setState({
        prevBtnState: "static"
      });
    }
  }
  render() {
    const page = this.props.states.global.getIn(["page", "cur"]);
    const section = this.props.states.global.getIn(["section", "cur"]);
    const hasPage = page !== null;
    const destPath = GlobalSelectors.getDestinationPathnameByOffset(this.props.states.global, 1);

    const transitionState = this.props.states.global.get("routeTransitionState");

    const hide = page !== this.props.pageKey || section !== this.props.sectionKey;
    const pageNaviCls = classNames({
      hide: hide || ["exited", "exiting", "entering"].includes(transitionState)
    });

    return (
      <div
        ref={r => (this.$.root = r)}
        key={this.props.pageKey}
        className={`transition ease-in-out fadeIn PageNavigation ${pageNaviCls}`}
      >
        <PageNavigationScrollLines
          states={this.props.states}
          scrollRef={this.props.scrollRef}
          pageKey={this.props.pageKey}
          sectionKey={this.props.sectionKey}
          height={this.state.pageHeight}
        />
        <PageNavigationLines
          pageKey={this.props.pageKey}
          sectionKey={this.props.sectionKey}
          hide={hide}
          hasPage={hasPage}
          prevBtnState={this.state.prevBtnState}
          nextBtnState={this.state.nextBtnState}
        />
        {destPath !== null &&
        !hide && ( //hide is needed so it doesn't flash when transitioning page
            <div className="PageNavigation__next-container">
              <div
                className="PageNavigation__next-container__btn transition short ease-in-out textMask left in "
                onMouseOver={this.onNextMouseOver}
                onMouseLeave={this.onNextMouseLeave}
                onClick={this.onNextClick}
              >
                {this.props.localizations.PageNavigation.next}
              </div>
            </div>
          )}
        {hasPage &&
        !hide && ( //hide is needed so it doesn't flash when transitioning page
            <div className="PageNavigation__prev-container">
              <div
                className="PageNavigation__prev-container__btn transition short ease-in-out textMask right in "
                onClick={this.onPrevClick}
                onMouseOver={this.onPrevMouseOver}
                onMouseLeave={this.onPrevMouseLeave}
              >
                {this.props.localizations.PageNavigation.prev}
              </div>
            </div>
          )}
      </div>
    );
  }
}

PageNavigation.propTypes = {
  sectionKey: PropTypes.string,
  pageKey: PropTypes.string, //this needs to be a valid path ie. about/intro
  onNextClick: PropTypes.func,
  states: PropTypes.shape({
    global: PropTypes.object.isRequired,
    router: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  scrollRef: PropTypes.object
};

PageNavigation.defaultProps = {
  onNextClick: () => {},
  sectionKey: null,
  pageKey: null,
  scrollRef: null
};
PageNavigation.componentName = "PageNavigation";

export default LocalizationEnhancer(["PageNavigation"])(PageNavigation);
