import { fromJS } from "immutable";
import { getPage,getSection } from "./global.selectors.js";



const _ = {
  flatten: require("lodash/flatten")
};

const sections = ["about", "creations", "contact","resume"];
const pages = {
  about: ["intro", "passion", "career"],
  creations: ["election2016","dashboard","meteorsim"],
  contact: [],
  resume: []
};

const initialState = fromJS({
  runningInElectron: window && window.process && window.process.type,
  routeTransitionState: null, // ["entering","entered","exiting","exited"]
  language: window.localStorage.getItem("language") || "en",
  section: {
    changingFrom:null,
    changingTo:null,
    prev: null,
    cur: null
  },
  page: {
    changingFrom:null,
    changingTo:null,
    prev: null,
    cur: null
  },
  pages: pages,
  sections: sections,
  pathNames: _.flatten(
    sections.map((section, i) => {
      const temp = pages[section].map(page => `/${section}/${page}`);
      temp.unshift(`/${section}`);
      return temp;
    })
  )
});

export { initialState };
export default { initialState };
