import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

class SectionTitle extends Component{
  constructor(props){
    super(props);
    this.onChangeDescription = this.onChangeDescription.bind(this);
    this.state = {
      isDisabled: false,
      descriptionIndex: 0
    };
    this.onChangeDescription = this.onChangeDescription.bind(this);
  }
  componentDidMount(){
    //first time component has mounted
  }
  componentWillUnmount(){
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps){
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps,nextState){
    return true;
  }
  componentWillUpdate(nextProps, nextState){
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState){
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onChangeDescription(e){
    let index = (this.state.descriptionIndex + 1) % this.props.descriptions.length;
    if(Number.isNaN(index)){
      index = 0;
    }
    this.setState({
      descriptionIndex: index
    });
  }
  render(){
    const emailCls = classNames({
      isEmail: this.props.title.includes("@")
    });
    return(
      <div className="SectionTitle animation fadeIn long ease " onClick={this.onChangeDescription}>
        <div className="SectionTitle__title">
          <span key={this.props.title} className={`transition short ease-in-out textMask left in ${emailCls}`}>
            { this.props.title }
          </span>
        </div>
        <div className="SectionTitle__description very-light-text animated fadeInLeft" key={this.state.descriptionIndex}>
          {this.props.descriptions[this.state.descriptionIndex] || " "}
        </div>
      </div>
    );
  }
}

SectionTitle.propTypes = {
  title: PropTypes.string,
  descriptions: PropTypes.arrayOf(PropTypes.string)
};

SectionTitle.defaultProps = {
  title: "",
  descriptions: []
};
SectionTitle.componentName = "SectionTitle";

export default SectionTitle;
