import { isImmutable } from "immutable";

function checkIfValidState(state){
  if(!isImmutable(state)){
    throw new Error("given state is not an immutable object");
  }
}

export function findPageIndex(curPage,pages){
  return pages.findIndex(page => page === curPage);
}
export function findSectionIndex(curSection,sections){
  return sections.findIndex(section => section === curSection);
}

export function findCurPageIndex(globalState){
  checkIfValidState(globalState);
  const currentSection = globalState.getIn(["section", "cur"]);
  const curPage = globalState.getIn(["page", "cur"]);
  const pages = globalState.getIn(["pages", currentSection]).toJS();
  return findPageIndex(curPage,pages);
}

export function getSection(pathname){
  return pathname.split("/")[1] || null;
}

export function getPage(pathname){
  return pathname.split("/")[2] || null;
}

export function getCurrentUrlPath(globalState){
  checkIfValidState(globalState);
  const currentPage = globalState.getIn(["page", "cur"]);
  const currentSection = globalState.getIn(["section", "cur"]);
  return `/${currentSection}${currentPage ? "/" + currentPage : ""}`;
}

export function getCurPathnameIndex(globalState){
  checkIfValidState(globalState);
  const curPath = getCurrentUrlPath(globalState);
  const pathNames = globalState.get("pathNames").toJS();
  return pathNames.findIndex(pathname => pathname === curPath);
}
export function getDestinationPathnameByOffset(globalState,offset){
  const pathNames = globalState.get("pathNames").toJS();
  const pathNameIndex = getCurPathnameIndex(globalState);
  return pathNames[ pathNameIndex + offset] || null;
}
