import React, { Component } from "react";
import PropTypes from "prop-types";
import classNames from "classnames";

import { Transition, config } from "react-spring";

import { NavigationMenuLines, NavigationMenuContent } from "./elements";
import { NavigationMenuBackground } from "./components";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";

class NavigationMenu extends Component {
  constructor(props) {
    super(props);
    this.state = {
      visible: false,
      lineState: "static", //static || hover || active
      hoverGridIndex: null,
      hoveringGridItem: false
    };
    this.timers = [];

    this.onMenuBtnClick = this.onMenuBtnClick.bind(this);
    this.onMenuBtnEnter = this.onMenuBtnEnter.bind(this);
    this.onMenuBtnLeave = this.onMenuBtnLeave.bind(this);
    this.onMenuItemHover = this.onMenuItemHover.bind(this);
    this.onMenuItemClick = this.onMenuItemClick.bind(this);
  }
  componentWillMount() {}
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps, nextState) {
    const languageChanged = this.props.language !== nextProps.language;
    const visibleChanged = this.state.visible || nextState.visible;
    if (visibleChanged || languageChanged) {
      return true;
    }
    return false;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  onMenuItemClick(section, page) {
    const destPath = "/" + section + (page ? "/" + page : "");
    this.props.actions.global.requestTransitionPush(destPath, 600, 100, 600);
    setTimeout(() => {
      this.setState({
        visible: false,
        lineState: "static"
      });
    }, 1300);
  }
  onMenuItemHover(gridIndex) {
    if (gridIndex === null) {
      this.setState({
        hoveringGridItem: false
      });
    } else {
      this.setState({
        hoverGridIndex: gridIndex,
        hoveringGridItem: true
      });
    }
  }
  onMenuBtnClick(e) {
    const visible = this.state.visible;
    if (visible) {
      this.setState({
        lineState: "static",
        visible: false
      });
    } else {
      this.setState({
        lineState: "active",
        visible: true
      });
    }
  }
  onMenuBtnEnter() {
    if (!["hover", "active"].includes(this.state.lineState)) {
      this.setState({
        lineState: "hover"
      });
    }
  }
  onMenuBtnLeave() {
    if (!["static", "active"].includes(this.state.lineState)) {
      this.setState({
        lineState: "static"
      });
    }
  }
  render() {
    if (this.props.states.global.get("runningInElectron")) {
      return null;
    }
    const menuText = this.state.visible
      ? this.props.localizations.NavigationMenu.close
      : this.props.localizations.NavigationMenu.menu;
    const maskCls = classNames({
      hide: !this.state.visible
    });
    const activeSection = this.props.states.global.getIn(["section", "cur"]);
    const activePage = this.props.states.global.getIn(["page", "cur"]);
    const pages = this.props.states.global.get("pages").toJS();
    const localizationNames = {
      ...this.props.localizations.Sections,
      ...this.props.localizations.Pages
    };
    return (
      <div className="NavigationMenu animation fadeIn long ease">
        <div
          className="NavigationMenu__button transition short ease-in-out textMask left in"
          onClick={this.onMenuBtnClick}
          onMouseOver={this.onMenuBtnEnter}
          onMouseLeave={this.onMenuBtnLeave}
        >
          {menuText}
        </div>
        <NavigationMenuLines state={this.state.lineState} />
        <div className={`NavigationMenu__mask ${maskCls}`} />
        <Transition
          config={{
            tension: 300,
            friction: 20,
            restSpeedThreshold: 0.01,
            restDisplacementThreshold: 0.01
          }}
          from={{
            opacity: 0,
            scale: 1.5
          }}
          enter={{
            opacity: 1,
            scale: 1
          }}
          leave={{
            opacity: 0,
            scale: 1.5
          }}
        >
          {this.state.visible &&
            (styles => (
              <div
                style={{
                  opacity: styles.opacity,
                  transform: `scale(${styles.scale})`
                }}
                className="NavigationMenu__content"
              >
                <div className="NavigationMenu__content__container">
                  <NavigationMenuBackground
                    states={this.props.states}
                    hoverGridIndex={this.state.hoverGridIndex}
                    isHovering={this.state.hoveringGridItem}
                  />
                  <NavigationMenuContent
                    localizationNames={localizationNames}
                    onItemClick={this.onMenuItemClick}
                    onHover={this.onMenuItemHover}
                    activeSection={activeSection}
                    activePage={activePage}
                    pages={pages}
                  />
                </div>
              </div>
            ))}
        </Transition>
      </div>
    );
  }
}

NavigationMenu.propTypes = {
  states: PropTypes.shape({
    global: PropTypes.object.isRequired
  }),
  actions: PropTypes.shape({
    global: PropTypes.object.isRequired
  })
};

NavigationMenu.defaultProps = {};
NavigationMenu.componentName = "NavigationMenu";

export default LocalizationEnhancer([
  "Sections",
  "Pages",
  "NavigationMenu.menu",
  "NavigationMenu.close"
])(NavigationMenu);
