import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";

import Parallax from "uiComponents/Parallax/Parallax.js";
import Page from "uiComponents/Page/Page.js";
import LazyLoad from "react-lazy-load";

import MeteorPic1 from "Assets/img/meteor1.jpg";
import MeteorPic2 from "Assets/img/meteor2.jpg";

import LocalizationEnhancer from "uiComponents/LocalizationEnhancer/LocalizationEnhancer.js";
import { shouldPageUpdate } from "clientUtils";

export class MeteorSim extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps, nextState) {
    return shouldPageUpdate(this.props,nextProps,"meteorsim");
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  render() {
    return (
      <Page
        states={this.props.states}
        actions={this.props.actions}
        title={this.props.localizations.MeteorSim.title}
        sectionKey="creations"
        pageKey="meteorsim"
        render={state => (
          <div id="MeteorSim">
            <Parallax scrollRef={state.root}>
              <div id="meteor-p1" className="p-container center">
                <p>
                  {this.props.localizations.MeteorSim.p1}
                </p>
              </div>
              <Parallax.Element align="center" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"80vw"}
                  width={"100%"}
                >
                  <div id="meteor1-pic" className="img-container border">
                    <img src={MeteorPic1} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
              <div id="meteor-p2" className="p-container center">
                <p>
                  {this.props.localizations.MeteorSim.p2}
                </p>
              </div>
              <Parallax.Element align="center" percentage={5} reverse={true}>
                <LazyLoad
                  offsetVerticle={-200}
                  throttle={50}
                  debounce={false}
                  height={"80vw"}
                  width={"100%"}
                >
                  <div id="meteor2-pic" className="img-container border">
                    <img src={MeteorPic2} />
                  </div>
                </LazyLoad>
              </Parallax.Element>
            </Parallax>
          </div>
        )}
      />
    );
  }
}

MeteorSim.propTypes = {
  //onComplete : PropTypes.func
};

MeteorSim.defaultProps = {
  //onComplete : ()=>{}
};
MeteorSim.componentName = "MeteorSim";

export default LocalizationEnhancer(["MeteorSim"])(MeteorSim);
