import React, { Component } from "react";
import PropTypes from "prop-types";
import { Router, Route, Link, browserHistory, IndexRoute } from "react-router";
import classNames from "classnames";

import shortid from "shortid";

import { SlideItem } from "./components";

class Slide extends Component {
  constructor(props) {
    super(props);
    this.state = {
      id: shortid.generate()
    };
    this.$slide = null;
    this.renderSlideItem = this.renderSlideItem.bind(this);
  }
  componentWillMount() {}
  componentDidMount() {
    //first time component has mounted
  }
  componentWillUnmount() {
    //when component is unmounted
  }
  componentWillReceiveProps(nextProps) {
    ////invoked when component is receiving new props
  }
  shouldComponentUpdate(nextProps, nextState) {
    return true;
  }
  componentWillUpdate(nextProps, nextState) {
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState) {
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  getTranslate(axis, activeIndex, numItems) {
    if (axis === "horizontal") {
      return `translate3d(${-100 / numItems * activeIndex}%,0px,0px)`;
    } else if (axis === "vertical") {
      return `translate3d(0px,${-100 * activeIndex}%,0px)`;
    }
  }
  renderSlideItem(numItems,activeIndex) {
    if (Array.isArray(this.props.children)) {
      return this.props.children.map((child, i) => {
        return (
          <SlideItem key={i} numItems={numItems} axis={this.props.axis}>
            {child}
          </SlideItem>
        );
      });
    } else if (this.props.children) {
      return (
        <SlideItem key={0} numItems={numItems} axis={this.props.axis}>
          {this.props.children}
        </SlideItem>
      );
    } else {
      return null;
    }
  }
  render() {
    let numItems = this.props.children.length;
    if (this.props.axis === "vertical") {
      numItems = 1;
    }
    const slideStyle = {
      transform: this.getTranslate(this.props.axis, this.props.activeIndex, numItems),
      width: 100 * numItems + "%"
    };
    return (
      <div
        style={slideStyle}
        ref={ref => (this.$slide = ref)}
        id={this.state.id}
        className={`Slide ${this.props.axis} ${this.props.className}`}
      >
        {this.renderSlideItem(numItems,this.props.activeIndex)}
      </div>
    );
  }
}

Slide.propTypes = {
  className: PropTypes.string,
  activeIndex: PropTypes.number,
  axis: PropTypes.oneOf(["horizontal", "vertical"]).isRequired
};

Slide.defaultProps = {
  className: "",
  activeIndex: 0
};

Slide.componentName = "Slide";

Slide.Item = SlideItem;

export default Slide;
