import React, { Component } from "react";
import PropTypes from "prop-types";
// import { Router, Route, Link , browserHistory, IndexRoute} from "react-router";
import classNames from "classnames";

import Line from "uiComponents/Line/Line.js";
import PageScrollEnhancer from "uiComponents/PageScrollEnhancer/PageScrollEnhancer";

export class PageNavigationScrollLines extends Component{
  constructor(props){
    super(props);
    this.state = {
      bottomLineState: "static"
    };
    this.scrollRef = null;
    this.bottomLineDebounce = null;

    this.update = this.update.bind(this);
    this.scrollHandler = this.scrollHandler.bind(this);
    this.getRatio = this.getRatio.bind(this);
    this.testIfActivateBottomLine = this.testIfActivateBottomLine.bind(this);
  }
  scrollHandler(event){
    this.props.scroll.handler(event,this.scrollRef);
  }
  update(props){
    if(this.scrollRef){
      this.scrollRef.removeEventListener("scroll",this.scrollHandler,false);
    }
    this.scrollRef = props.scrollRef;
    if(this.scrollRef){
      this.scrollRef.addEventListener("scroll",this.scrollHandler,false);
    }
  }
  componentDidMount(){
    //first time component has mounted
    this.update(this.props);
  }
  componentWillUnmount(){
    //when component is unmounted
    if(this.bottomLineDebounce){
      clearTimeout(this.bottomLineDebounce);
    }
    if(this.scrollRef){
      this.scrollRef.removeEventListener("scroll",this.scrollHandler,false);
    }
  }
  componentWillReceiveProps(nextProps){
    ////invoked when component is receiving new props
    this.update(nextProps);
  }
  shouldComponentUpdate(nextProps,nextState){
    return true;
  }
  componentWillUpdate(nextProps, nextState){
    ////invoked immediately before rendering when new props or state are being received (not called on initial render)
    ////cannot use this.setState() in this method, use componentWillReceiveProps instead
  }
  componentDidUpdate(prevProps, prevState){
    //invoked immediately after component's updates are flushed to the DOM. this method is not called for initial render
    //this is an opportunity to operate on the DOM when the component has been updated.
  }
  getRatio(){
    const scrollY = this.props.scroll.scrollY;
    const height = Math.max(this.props.height - window.innerHeight,1);
    let ratio = Math.min(scrollY/height,1);
    ratio = ratio > 0.95 ? 1 : ratio;
    return ratio;
  }
  testIfActivateBottomLine(sideLineRatio){
    if(sideLineRatio === 1){
      if(this.bottomLineDebounce){
        clearTimeout(this.bottomLineDebounce);
      }
      this.bottomLineDebounce = setTimeout(()=>{
        if(this.state.bottomLineState !== "active"){
          this.setState({
            bottomLineState: "active"
          });
        }
      },1000);
    } else {
      if(this.state.bottomLineState !== "static"){
        this.setState({
          bottomLineState: "static"
        });
      }
    }
  }
  render(){
    const page = this.props.states.global.getIn(["page", "cur"]);
    const section = this.props.states.global.getIn(["section", "cur"]);
    const ratio = this.getRatio();
    this.testIfActivateBottomLine(ratio);

    if(this.props.pageKey !== page || this.props.sectionKey !== section){
      return null;
    }

    return(
      <div className="PageNavigationScrollLines" >
        <Line
          className="PageNavigationScrollLines__side"
          direction={"down"}
          placement={"left"}
          state={"interpolate"}
          points={{
            interpolate:{
              start: "10px",
              end: `calc(calc(100% - 10px) * ${1 - ratio})`
            }
          }}
        />
        <Line
          className="PageNavigationScrollLines__bottom"
          direction={"right"}
          placement={"bottom"}
          state={this.state.bottomLineState}
          points={{
            static: {
              start:0,
              end:0
            },
            active:{
              start: 0,
              end: "calc(100% - 25px)"
            }
          }}
        />
      </div>
    );
  }
}

PageNavigationScrollLines.propTypes = {
  sectionKey: PropTypes.string,
  pageKey: PropTypes.string, //this needs to be a valid path ie. about/intro
  height: PropTypes.number,
  states: PropTypes.shape({
    global: PropTypes.object.isRequired,
    router: PropTypes.object.isRequired
  }),
  scrollRef: PropTypes.object
};

PageNavigationScrollLines.defaultProps = {
  scrollRef: null,
  height: 9999
};
PageNavigationScrollLines.componentName = "PageNavigationScrollLines";

export default PageScrollEnhancer(PageNavigationScrollLines);
