import React from "react";
import PropTypes from "prop-types";

import Line from "uiComponents/Line/Line.js";

const PageNavigationLines = props => {
  if (props.hide) {
    return null;
  }

  return (
    <div className="PageNavigationLines">
      {props.hasPage && (
        <Line
          className="PageNavigationLines__prev"
          direction={"left"}
          placement={"top"}
          state={props.prevBtnState}
        />
      )}
      <Line
        className="PageNavigationLines__next"
        direction={"right"}
        placement={"bottom"}
        state={props.nextBtnState}
      />
    </div>
  );
};
PageNavigationLines.propTypes = {
  pageKey: PropTypes.string, //this needs to be a valid path ie. about/intro
  hasPage: PropTypes.bool,
  hide: PropTypes.bool,
  prevBtnState: PropTypes.oneOf(["static", "hover", "enter", "active", "exit"]),
  nextBtnState: PropTypes.oneOf(["static", "hover", "enter", "active", "exit"])
};
PageNavigationLines.defaultProps = {
  pageKey: null,
  hasPage: false,
  hide: false
};
PageNavigationLines.componentName = "PageNavigationLines";

export default PageNavigationLines;
